
@import "./assets/css/variable.scss";
#app {
  display: flex;
  @include portrait {
    flex-direction: column;
  }
}
h1 {
  color: #925e47;
  font-size: 32px;
  font-weight: 900;
  margin-bottom: 6px;
}
.left_side {
  width: 500px;
  height: 100vh;
  overflow-y: auto;
  padding: 30px;
  text-align: center;
  @include portrait {
    width: 100vw;
    height: unset;
    overflow-y: unset;
  }
  .card {
    position: relative;
    text-align: left;
    padding: 0px 14px 16px 14px;
    margin: 16px 0px;
    border-radius: 10px;
    box-shadow: #949da421 0px 6px 12px;
    overflow: hidden;
    &.bg_1 {
      background: #fff7d0;
    }
    &.bg_2 {
      background: #ffe8bd;
    }
    &.bg_3 {
      background: #ffd9ad;
    }
    &::after {
      position: absolute;
      content: "";
      width: 300px;
      height: 120%;
      background: #ffffff69;
      border-radius: 99rem;
      top: -40%;
      left: -80px;
      filter: blur(60px);
      z-index: 0;
    }
  }
  .content {
    position: relative;
    z-index: 5;
    .row {
      display: flex;
      align-items: center;
      padding: 4px 0px;
      @include portrait {
        flex-wrap: wrap;
      }
      &.option {
        .option_item {
          width: 100px;
        }
        input {
          width: 20px;
          margin: 0px;
        }
      }
      > * {
        color: #636363;
        white-space: nowrap;
        line-height: 1.2;
        border-radius: 4px;
        vertical-align: middle;
      }
      .display {
        display: inline-block;
        width: 100%;
        padding: 2px 4px;
        margin: 0px 12px 0px 4px;
        background: #ffffffa4;
        @include portrait {
          margin: 0px;
        }
      }
      input {
        width: 100%;
        margin: 0px 12px 0px 4px;
        background: #fff;
        padding: 0px 4px;
        line-height: 1.5;
        border: #ff823ec4 1px solid;
        &:disabled {
          background: #cfcfcf;
        }
        @include portrait {
          margin: 0px;
        }
      }
      :nth-last-of-type(1) {
        margin-right: 0px;
      }
      .bold {
        font-weight: bold;
        margin-right: 6px;
      }
    }
  }
}
.wrap_step {
  position: relative;
  display: inline-block;
  width: 105px;
  vertical-align: middle;
  z-index: 5;
  .step {
    text-align: center;
    display: inline-block;
    font-size: 95px;
    color: #ff9f6b3a;
    font-weight: 900;
    font-family: Helvetica, Arial, sans-serif;
  }
  .icon {
    position: absolute;
    top: 25px;
    right: 10px;
    display: inline-block;
    color: #ff823e;
    font-size: 52px;
    opacity: 0.6;
  }
}
.card_title {
  position: relative;
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  z-index: 5;
  h2 {
    color: #925e47;
    font-size: 24px;
    text-align: left;
    line-height: 1.5;
  }
  .sub {
    color: #ff884a;
  }
}
.render_button {
  width: 100%;
  color: #fff;
  padding: 16px;
  background: #ff884a;
  border-radius: 99rem;
  box-shadow: #949da421 0px 6px 12px;
  cursor: pointer;
  &:disabled {
    background: #ff894a8f;
  }
}
.right_side {
  width: calc(100vw - 500px);
  height: 100vh;
  min-width: 425px;
  min-height: 400px;
  max-height: unset;
  text-align: center;
  background: #f5f5f5;
  padding: 20px;
  @include portrait {
    width: 100vw;
    height: unset;
  }
  p {
    text-align: center;
    line-height: 150px;
  }
}
.pdfwrap {
  height: 100%;
  iframe {
    width: 100%;
    height: 100%;
  }
  @include portrait {
    height: 500px;
  }
}
input[type="file"] {
  padding: 12px 0px 0px 16px;
  color: #636363;
}
