@charset "utf-8";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
audio,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
mark,
menu,
meter,
nav,
output,
progress,
section,
summary,
time,
video {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: top;
}

body {
    line-height: 1;
    font-size: 14px;
    position: relative;
}

article,
aside,
dialog,
figure,
footer,
header,
hgroup,
nav,
section,
blockquote {
    display: block;
}

nav ul {
    list-style: none;
}

ol {
    list-style: decimal;
}

ul,
li {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}

ins {
    text-decoration: underline;
}

del {
    text-decoration: line-through;
}

mark {
    background: none;
}

abbr[title],
dfn[title] {
    border-bottom: 1px dotted #000;
    cursor: help;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

input[type="submit"],
input[type="button"],
button {
    padding: 0;
    margin: 0;
}

input,
select,
a img {
    vertical-align: middle;
}
.clr {
    clear: both;
    display: block;
    margin: 0;
    padding: 0;
    line-height: 0;
}
.toLeft {
    float: left;
}
.toRight {
    float: right;
}
.alignLeft {
    text-align: left;
}
.alignRight {
    text-align: right;
}
a {
    text-decoration: none;
}
button {
    background: transparent;
    border: none;
}
* {
    box-sizing: border-box;
    -webkit-text-size-adjust: none;
    font-family: "Noto Sans TC", "微軟正黑體", "Microsoft JhengHei", Arial, Helvetica, sans-serif;
}